<script>
    export let dotSize = 13;
    export let leftSize = 8;
    export let bgColor = "#a0aec0";
</script>

<div class="w-full flex items-center justify-center">
    <div
        class="lds-ellipsis"
        style=" --t-size: {leftSize * 3 + 'px'};--bg-color:{bgColor}"
    >
        <div
            style="   width: {dotSize}px;
        height:  {dotSize}px; 
        left: {leftSize}px;
        "
        />
        <div
            style="   
           
            width: {dotSize}px;
        height:  {dotSize}px;      
        left:  {leftSize}px;
        "
        />
        <div
            style="   width: {dotSize}px;
        height:  {dotSize}px;
        left:  {leftSize *
                4}px;
        "
        />
        <div
            style="   width: {dotSize}px;
        height:  {dotSize}px;
        left:  {leftSize *
                7}px;
        "
        />
    </div>
</div>

<style lang="postcss">.lds-ellipsis{display:inline-block;position:relative;width:60px}.lds-ellipsis div{position:absolute;border-radius:50%;background:var(--bg-color);-webkit-animation-timing-function:cubic-bezier(0,1,1,0);animation-timing-function:cubic-bezier(0,1,1,0)}.lds-ellipsis div:first-child{-webkit-animation:lds-ellipsis1 .6s infinite;animation:lds-ellipsis1 .6s infinite}.lds-ellipsis div:nth-child(2),.lds-ellipsis div:nth-child(3){-webkit-animation:lds-ellipsis2 .6s infinite;animation:lds-ellipsis2 .6s infinite}.lds-ellipsis div:nth-child(4){-webkit-animation:lds-ellipsis3 .6s infinite;animation:lds-ellipsis3 .6s infinite}@-webkit-keyframes lds-ellipsis1{0%{transform:scale(0)}to{transform:scale(1)}}@keyframes lds-ellipsis1{0%{transform:scale(0)}to{transform:scale(1)}}@-webkit-keyframes lds-ellipsis3{0%{transform:scale(1)}to{transform:scale(0)}}@keyframes lds-ellipsis3{0%{transform:scale(1)}to{transform:scale(0)}}@-webkit-keyframes lds-ellipsis2{0%{transform:translate(0)}to{transform:translate(var(--t-size))}}@keyframes lds-ellipsis2{0%{transform:translate(0)}to{transform:translate(var(--t-size))}}
</style>
